import React, {useState, useCallback } from 'react'
import styled from 'styled-components'
import { Route, useRouteMatch, NavLink, useLocation } from 'react-router-dom'
import { Text, Flex, Input } from '@pancakeswap-libs/uikit'
import BigNumber from 'bignumber.js'
import { BLOCKS_PER_YEAR, CAKE_PER_BLOCK, CAKE_POOL_PID } from 'config'
import SearchInput from 'components/SearchInput'
import Grid from 'components/layout/Grid'
import { useFarmFromSymbol, useFarms, usePriceBnbBusd, useTotalValue } from 'state/hooks'
import { PoolCategory, QuoteToken} from 'config/constants/types'
import useI18n from 'hooks/useI18n'
import Page from 'components/Page'
import FarmCard, { FarmWithStakedValue } from './components/FarmCard'
import FarmTabButtons from './components/FarmTabButtons'   
 

const Farms: React.FC = () => {
  const { path } = useRouteMatch()
  const { pathname } = useLocation()
  const TranslateString = useI18n()
  const farmsLP = useFarms()
  const bnbPrice = usePriceBnbBusd()
  const [query, setQuery] = useState('')
  const isArchived = pathname.includes('archived')
  const isInactive = pathname.includes('history')
  const isActive = !isInactive && !isArchived
  
  const activeFarms = farmsLP.filter((farm) => farm.pid !== 0 && farm.multiplier !== '0X')
  const inactiveFarms = farmsLP.filter((farm) => farm.pid !== 0 && farm.multiplier === '0X')
  const newFarms = farmsLP.filter((farm) => farm.pid !== 0 && farm.tokenSymbol2  === 'PARTNER')
  const comuFarms = farmsLP.filter((farm) => farm.pid !== 0 && farm.tokenSymbol3  === 'COMMUNITY')
 
  
  const handleChangeQuery = (event: React.ChangeEvent<HTMLInputElement>) => {
    setQuery(event.target.value)
  }
  
    
 
  const farmsList = useCallback(
    (farmsToDisplay, removed: boolean) => {
      const cakePriceVsBNB = new BigNumber(farmsLP.find((farm) => farm.pid === CAKE_POOL_PID)?.tokenPriceVsQuote || 0)
      const farmsToDisplayWithAPY: FarmWithStakedValue[] = farmsToDisplay.map((farm) => {
        if (!farm.tokenAmount || !farm.lpTotalInQuoteToken || !farm.lpTotalInQuoteToken) {
          return farm
        }
        const cakeRewardPerBlock = CAKE_PER_BLOCK.times(farm.poolWeight)
        const cakeRewardPerYear = cakeRewardPerBlock.times(BLOCKS_PER_YEAR)

        let apy = cakePriceVsBNB.times(cakeRewardPerYear).div(farm.lpTotalInQuoteToken)

        if (farm.quoteTokenSymbol === QuoteToken.BUSD) {
          apy = cakePriceVsBNB.times(cakeRewardPerYear).div(farm.lpTotalInQuoteToken).times(bnbPrice)
        } else if (farm.quoteTokenSymbol === QuoteToken.ZEUS) {
          apy = cakeRewardPerYear.div(farm.lpTotalInQuoteToken)
        } else if (farm.dual) {
          const cakeApy =
            farm && cakePriceVsBNB.times(cakeRewardPerBlock).times(BLOCKS_PER_YEAR).div(farm.lpTotalInQuoteToken)
          const dualApy =
            farm.tokenPriceVsQuote &&
            new BigNumber(farm.tokenPriceVsQuote)
              .times(farm.dual.rewardPerBlock)
              .times(BLOCKS_PER_YEAR)
              .div(farm.lpTotalInQuoteToken)

          apy = cakeApy && dualApy && cakeApy.plus(dualApy)
        }

        return { ...farm, apy }
      })
      return farmsToDisplayWithAPY.map((farm) => <FarmCard key={farm.pid} farm={farm} removed={removed} />)
    },
    [bnbPrice, farmsLP],
  )
 

  return (
    <Page>
      <Title>{TranslateString(999, 'Stake LP tokens to earn ZEUS')}</Title>
      <StyledLink exact activeClassName="active" to="/staking">
        Staking
      </StyledLink>
       
      <FarmTabButtons/>
  
      <FilterContainer>
      <ControlContainer>
   
      <LabelWrapper>
              <SearchInput placeholder='searchFarms' onChange={handleChangeQuery}/>                         
      </LabelWrapper>
      </ControlContainer>
      </FilterContainer>

            <Route exact path={`${path}`}>
          <Grid>{farmsList(activeFarms, false)}</Grid>
        </Route>
        <Route exact path={`${path}/history`}>
          <Grid>{farmsList(inactiveFarms, true)}</Grid>
        </Route>
        <Route exact path={`${path}/new`}>
          <Grid>{farmsList(newFarms, false)}</Grid>
        </Route>
        <Route exact path={`${path}/community`}>
          <Grid>{farmsList(comuFarms, false)}</Grid>
        </Route>
         
     </Page>
  )
}

 
 

const StyledLink = styled(NavLink)`
  display: none;
  @media (max-width: 400px) {
    display: block;
    background: #50d7dd;
    border-radius: 5px;
    line-height: 40px;
    font-weight: 900;
    padding: 0 20px;
    margin-bottom: 30px;
    color: #fff;
  }
`
 
const ControlContainer = styled.div`
  display: flex;
  width: 50%;
  align-items: center;
  position: relative;

  justify-content: space-between;
  flex-direction: column;
  margin-bottom: 32px;
}
`
const LabelWrapper = styled.div`
  > ${Text} {
    font-size: 12px;
  }
`

const Image = styled.img`
  @media (max-width: 500px) {
    width: 100vw;
  }
`

const Title = styled.div`
  color: ${(props) => props.theme.colors.secondary};
  font-size: 29px;
  width: 50vw;
  text-align: center;
  font-weight: 900;
  margin: 50px;
`
const FilterContainer = styled.div`
  display: flex;
  align-items: center;
  width: 100%;
  padding: 8px 0px;
  `

export default Farms
