import React from 'react'
import { Card, CardBody, Heading, Text } from '@pancakeswap-libs/uikit'
import BigNumber from 'bignumber.js/bignumber'
import styled from 'styled-components'
import { getBalanceNumber } from 'utils/formatBalance'
import { useTotalSupply, useBurnedBalance } from 'hooks/useTokenBalance'
import useI18n from 'hooks/useI18n'
import { getCakeAddress } from 'utils/addressHelpers'
import CardValue from './CardValue'
import { useFarms, usePriceCakeBusd } from '../../../state/hooks'

const StyledCakeStats = styled(Card)`
  margin-left: auto;
  margin-right: auto;
  opacity: 0.92;
`

const Row = styled.div`
  align-items: center;
  display: flex;
  font-size: 14px;
  justify-content: space-between;
  margin-bottom: 8px;
`
 

const CakeStats = () => {
  const TranslateString = useI18n()
  const totalSupply = useTotalSupply()
  const burnedBalance = useBurnedBalance(getCakeAddress())
  const farms = useFarms();
  const eggPrice = usePriceCakeBusd();
  const circSupply = totalSupply ? totalSupply.minus(burnedBalance) : new BigNumber(0);
  const cakeSupply = getBalanceNumber(circSupply);
  const marketCap = eggPrice.times(circSupply);
   
 

  return (
    <StyledCakeStats>
      <CardBody>
        <Heading size="lg" mb="20px" color='secondary'>
          {TranslateString(534, 'Zeus Stats')}
        </Heading>
        <Row>
          <Text fontSize="17px" color='primary'>{TranslateString(10005, 'Market Cap')}</Text>
          <CardValue fontSize="17px" value={getBalanceNumber(marketCap)} decimals={0} prefix="$" />
        </Row>
        <Row>
          <Text fontSize="17px" color='primary'>{TranslateString(536, 'Total Minted')}</Text>
          {totalSupply && <CardValue fontSize="17px" value={getBalanceNumber(totalSupply)} decimals={0} />}
        </Row>
        <Row>
          <Text fontSize="17px" color='primary'>{TranslateString(538, 'Total Burned')}</Text>
          <CardValue fontSize="17px" value={getBalanceNumber(burnedBalance)} decimals={0} />
        </Row>
        <Row>
          <Text fontSize="17px" color='primary'>{TranslateString(10004, 'Circulating Supply')}</Text>
          {cakeSupply && <CardValue fontSize="17px" value={cakeSupply} decimals={0} />}
        </Row>
       
      </CardBody>
    </StyledCakeStats>
  )
}

export default CakeStats
