import React from 'react'
import styled from 'styled-components'
import {useLocation, useRouteMatch, Link } from 'react-router-dom'
import { ButtonMenu, ButtonMenuItem, Button, Text, Toggle} from '@pancakeswap-libs/uikit'
import useI18n from 'hooks/useI18n'
 
const FarmTabButtons = () => {
  const { url } = useRouteMatch()
  const TranslateString = useI18n()
  const location = useLocation()


  let activeIndex
  switch (location.pathname) {
    case '/All':
      activeIndex = 0
      break
    case '/farms/partners':
      activeIndex = 1
      break
    case '/farms/community':
      activeIndex = 2
      break
      case '/farms/history':
        activeIndex = 3
        break
    default:
      activeIndex = 0
      break
  }

  return (
    <>
     <Wrapper>
  
<ButtonMenu activeIndex={activeIndex} variant="primary">  
          <ButtonMenuItem as={Link} to={`${url}`}>
          {TranslateString(999, 'All')}
        </ButtonMenuItem>
        <ButtonMenuItem as={Link} to={`${url}/partners`}>
          {TranslateString(999, 'Partners')}
          </ButtonMenuItem>
          <ButtonMenuItem as={Link} to={`${url}/community`}>
          {TranslateString(999, 'Community')}
          </ButtonMenuItem>
          <ButtonMenuItem as={Link} to={`${url}/history`}>
          {TranslateString(999, 'Archived')}
          
        </ButtonMenuItem>
          </ButtonMenu>
        
          </Wrapper>
              </>
  );
}
export default FarmTabButtons

const Wrapper = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 32px;
`
const ToggleWrapper = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 32px;

  ${Text} {
    margin-left: 8px;
  }
`